.popup__container {
  margin: 0;
  border-radius: 3%;
  position: relative;
  background-color: rgba(255, 255, 255, 1);
  width: 430px;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 767px) {
  .popup__container {
    width: 282px;
  }
}

.popup__container:hover {
  cursor: auto;
}
