.popup__close-button {
  width: 40px;
  height: 40px;
  background: no-repeat center url(../../../images/form__close.svg);
  border: none;
  margin-top: 1px;
  position: absolute;
  top: -45px;
  right: -44px;
}

.popup__close-button:hover {
  opacity: 0.6;
  transition: opacity 0.2s ease-out;
  cursor: pointer;
}

.popup__close-button:focus {
  outline: 0;
}

@media screen and (max-width: 767px) {
  .popup__close-button {
    width: 26px;
    height: 26px;
    top: -45px;
    right: 0;
  }
}
